import { Button, Drawer, List, message, Typography } from "antd";
import { useEffect, useState } from "react";
import { checkout, getCart, removeCartItem } from "../utils";
import { CloseOutlined } from "@ant-design/icons";

const { Text } = Typography;

const MyCart = () => {
    const [cartVisible, setCartVisible] = useState(false);
    const [cartData, setCartData] = useState();
    const [loading, setLoading] = useState(false);
    const [checking, setChecking] = useState(false);

    // 
    const fetchCartData = () => {
        setLoading(true);
        getCart()
            .then((data) => {
                setCartData(data);
            })
            .catch((err) => {
                message.error(err.message);
            })
            .finally(() => {
                setLoading(false);
            });
    };

    useEffect(() => {
        if (cartVisible) {
            fetchCartData();
        }
    }, [cartVisible]);

    // 2. 
    const onRemoveItem = (orderItemId) => {
        setLoading(true); //  loading 
        removeCartItem(orderItemId)
            .then(() => {
                message.success("Item removed");
                fetchCartData(); // 
            })
            .catch((err) => {
                message.error(err.message);
            })
            .finally(() => {
                setLoading(false);
            });
    };

    const onCheckOut = () => {
        setChecking(true);
        checkout()
            .then(() => {
                message.success("Successfully checkout");
                setCartVisible(false);
            })
            .catch((err) => {
                message.error(err.message);
            })
            .finally(() => {
                setChecking(false);
            });
    };

    const onCloseDrawer = () => setCartVisible(false);
    const onOpenDrawer = () => setCartVisible(true);

    const orderItems = cartData?.orderItems ?? [];
    const totalPrice = cartData?.totalPrice ?? 0;

    return (
        <>
            <Button type="primary" shape="round" onClick={onOpenDrawer}>
                Cart
            </Button>

            <Drawer
                title="My Shopping Cart"
                onClose={onCloseDrawer}
                open={cartVisible}
                width={520}
                footer={
                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                        <Text strong>{`Total price: $${totalPrice.toFixed(2)}`}</Text>
                        <div>
                            <Button onClick={onCloseDrawer} style={{ marginRight: 8 }}>
                                Cancel
                            </Button>
                            <Button
                                onClick={onCheckOut}
                                type="primary"
                                loading={checking}
                                disabled={loading || orderItems.length === 0}
                            >
                                Checkout
                            </Button>
                        </div>
                    </div>
                }
            >
                <List
                    loading={loading}
                    itemLayout="horizontal"
                    dataSource={orderItems}
                    renderItem={(item) => (
                        <List.Item
                            // 3.  Remove 
                            actions={[
                                <Button
                                    type="text"      //  text 
                                    shape="circle"   // 
                                    icon={<CloseOutlined style={{ color: '#bfbfbf', fontSize: '16px' }} />}
                                    onClick={() => onRemoveItem(item.orderItemId)}
                                >
                                </Button>
                            ]}
                        >
                            <List.Item.Meta
                                title={item.menuItemName}
                                description={
                                    <Text type="secondary">
                                        {`Quantity: ${item.quantity} | Total: $${item.price.toFixed(2)}`}
                                    </Text>
                                }
                            />
                        </List.Item>
                    )}
                />
            </Drawer>
        </>
    );
};

export default MyCart;